import { AnnotationVisibilityChangeEventType } from '../../types/EventTypes';
import { getRenderingEngines } from '@cornerstonejs/core';
import { triggerAnnotationRenderForViewportIds } from '../../utilities';

export default function annotationVisibilityListener(
  evt: AnnotationVisibilityChangeEventType
) {
  const annotationUIDs = [].concat(evt.detail.lastHidden, evt.detail.lastVisible);
  if (!annotationUIDs) {
    return;
  }

  const renderingEngines = getRenderingEngines();

  renderingEngines.forEach((renderingEngine) => {
    const viewports = renderingEngine.getViewports();
    const viewportIds = viewports.map((vp) => vp.id);
    triggerAnnotationRenderForViewportIds(renderingEngine, viewportIds);
  });
}
